export const getSellerCounterOfferFormData = /* GraphQL */ `
	query Formdata_GetSellerCounterOffer(
		$CounterOfferId: String!
		$FormId: String!
	) {
		formdata_GetSellerCounterOffer(
			CounterOfferId: $CounterOfferId
			FormId: $FormId
		) {
			Form {
				Template
			}
			FormData {
				DataStatus
				DataValue
			}
			FormProperty
		}
	}
`;

